.header {
	&__inner {
		max-width: 1170px;
		padding: 0 15px;
		margin: 0 auto; }
	&__top {
		@include clr; }
	&__logo {
		float: left;
		margin: 20px 25px 20px 0; }
	&__contacts {
		float: left;
		margin-top: 30px; }
	&__social {
		float: left;
		margin: 40px 30px 0 15px; }
	&__in {
		float: left;
		position: relative; }
	&__search {
		margin-top: 29px; }
	&__login {
		display: inline-block;
		color: #4b4b4b;
		font-family: "Open Sans";
		font-size: 14px;
		font-weight: bold;
		line-height: 18px;
		margin: 13px 16px 10px 4px;
		&:hover {
			text-decoration: none; } }
	&__shopping-cart {
		float: right;
		margin-top: 12px; }

	&__info {
		min-height: 205px;
		background-color: $primary-color;
		@include cover;
		padding-left: 100px; }
	&__title {
		color: #ffffff;
		font-size: 30px;
		font-weight: 500; }
	&__text {
		color: #ffffff;
		font-size: 18px;
		line-height: 20px; } }

.social {
	&__link {
		margin: 3px 0 12px;
		display: block; }
	&__icon {
		display: inline-block;
		width: 16px;
		height: 16px;
		background-repeat: no-repeat;
		background-position: center; } }
// ------------main-search------------
.main-search {
	position: relative; }
.main-search__area {
	width: 310px;
	height: 35px;
	padding: 11px 65px 11px 14px;
	border-radius: 3px 4px 4px 3px;
	border: 1px solid #e6e6e6;
	background-color: #f8f8f8; }
.main-search__btn {
	width: 60px;
	height: 35px;
	font-size: 18px;
	border-radius: 3px 4px 4px 3px;
	background-color: $primary-color;
	position: absolute;
	top: 0;
	right: 0;
	.icon-search {
		position: relative;
		top: 2px; } }

.contacts {
	padding-left: 63px - 25px;
	position: relative;
	.icon-phone {
		position: absolute;
		left: 0;
		top: 16px;
		font-size: 25px;
		fill: #000; }
	&__number {
		color: #494949;
		font-size: 16px;
		font-weight: 500;
		line-height: 22px;
		&:hover {
			text-decoration: none; } } }

//--------------shopping-cart--------------
.shopping-cart {
	width: 208px;
	height: 88px;
	padding: 10px;
	border-radius: 3px;
	border: 2px solid $primary-color;
	&__line {
		position: relative;
		color: #333333;
		line-height: 18px;
		font-size: 14px; }
	&__text {
		display: inline-block;
		width: 101px;
		border-bottom: 1px solid #f1efef; }
	&__quantity {
		position: absolute;
		left: 85px;
		display: inline-block;
		font-size: 16px; }
	&__button {
		position: relative;
		display: block;
		text-align: right;
		margin-top: 13px;
		padding-right: 5px;
		color: $primary-color;
		font-size: 16px;
		font-weight: 700;
		line-height: 18px;
		.icon-shopping-cart {
			position: absolute;
			top: 2px;
			left: 0; }
		&:hover {
			text-decoration: none; } } }
//-----------------menu------------------
.menu {
	box-shadow: 0 6px 25px 2px rgba(111, 110, 110, 0.13);
	border: 2px solid $primary-color;
	font-size: 0;
	padding: 0 10px;
	&>.menu__item {
		display: inline-block;
		&>.menu__link {
			display: block;
			padding: 12px 15px;
			color: #4f4e4e;
			font-size: 16px;
			font-weight: 400;
			&:hover {
				font-weight: 500;
				text-decoration: none; } } } }


// plant-variety
.plant-variety {
	box-shadow: 0 6px 8px 1px rgba(114, 114, 114, 0.19);
	border: 1px solid #c1c1c1;
	background-color: #21973f;
	&__list {
		text-align: justify;
		padding: 0 5px; }
	&__item {
		display: inline-block;
		&:not(:last-child):after {
			content: "";
			display: inline-block;
			width: 1px;
			height: 28px;
			position: relative;
			top: 7px;
			background-color: #dadada; } }
	&__link {
		display: inline-block;
		padding: 0 35px;
		color: #ffffff;
		font-size: 16px;
		font-weight: 500;
		line-height: 49px;
		&:hover {
			text-decoration: none; } } }
